<template>
  <div class="table-responsive">
    <table class="table mb-0">
      <thead class="thead-default">
      <tr>
        <th>#</th>
        <th>ニックネーム　</th>
        <th>設問番号</th>
        <th>紹介者ID</th>
        <th>これまでの回答回数</th>
        <th>年代</th>
        <th>性別</th>
        <th>スコア</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(value, key, index) in listData">
        <th scope="row">{{value.id}}</th>
        <td>{{value.param.nickname}}</td>
        <td>{{value.param.improve}}</td>
        <td>{{value.param.code}}</td>
        <td>{{value.param.times}}</td>
        <td>{{value.param.age}}</td>
        <td>{{value.param.gender}}</td>
        <td>{{value.param.score}}</td>
        <td>
          <buttom class="btn btn-warning" @click="deleteItem(value.id)">删除</buttom>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import $ from 'jquery';

  export default {
    name: "list",
    data() {
      return {
        listData: []
      }
    },
    mounted() {
      this.getList();
    },
    methods: {
      deleteItem(id) {
        const _this = this;
        console.log(id)
        $.ajax({
          method: "POST", // 一般用 POST 或 GET 方法
          url: "https://115.29.201.200/api/del", // 要请求的地址
          data:{
            //发送到服务器的数据。将自动转换为请求字符串格式。GET 请求中将附加在 URL 后。查看 processData 选项说明以禁止此自动转换。
            // 必须为 Key/Value 格式。如果为数组，jQuery 将自动为不同值对应同一个名称。如 {foo: ["bar1", "bar2"]} 转换为 "&foo=bar1&foo=bar2"。
            id,
          },
          success(res) {
            console.log(res);// 成功之后执行这里面的代码
            _this.getList();
          },
          error(e){
            console.log(e)//请求失败是执行这里的函数
          }
        });
      },
      getList() {
        this.listData = [];
        this.$http.get('https://115.29.201.200/api/getList', {}).then(function (response) {
          const arr = response.body.msg;
          console.log('接口返回数据', arr);
          for (let i = 0; i < arr.length; i++) {
            // console.log(JSON.parse(arr[i].param))
            this.listData.push({
              id: arr[i].id,
              param: JSON.parse(arr[i].param)
            })
          }
          console.log(this.listData);
          // response.data中获取ResponseData实体
        }, function (response) {
          // 发生错误
        });
      },
    }
  }
</script>

