<template>
    <div class="wrapper">
        <TopLanguageSelector></TopLanguageSelector>
        
        <header>
            <h1><img class="title_logo img-fluid" src="../assets/logo.png" alt="減災教室"/></h1>
        </header>
      
        <main class="container">
            <div class="catchcopy">
                <h2 class="point" v-html="body.catchcopy.point"></h2>
                <p class="short" v-html="body.catchcopy.short"></p>
                <h2 class="try" v-html="body.catchcopy.try"></h2>
                <img src="../assets/takagi/1.png" width="30%" height="30%" class="img-fluid"/>
            </div>

            <TopLevelSelectors v-for="(info, i) in body.levelSelectors" :info="info" :key="i" />

            <p class="privacy-policy-title" v-text="body.privacyPolicy"></p>
            <div id="maindisp" style="height: 400px; background-color: rgba(255, 255, 255, 0.8)" >
                <iframe src="../../policy.html" id="policy" class="privacy-policy-iframe" name="privacy-policy-iframe" width="70%" height="40%"></iframe>
            </div>
        </main>

        <footer class="copyrights">
			編著：岐阜大学教授 髙木朗義<br>
