</template>

<script>
import top from '../assets/json/top.json'
import TopLevelSelectors from './TopLevelSelectors'
import TopLanguageSelector from './parts/TopLanguageSelector'

export default {
    components: {
        TopLevelSelectors,
        TopLanguageSelector
    },
    props: [
        'lang'
    ],
    data: function() {
        return {
            /* 問題フォルダから自動的に取得できるようにしたい */
            /* 問題集のタイトル、提供元、リンク先などの情報 */
            top: top
        };
    },
    computed: {
        body: function() {
            if(this.$props.lang === "en"){
                return top.en;
            }else if(this.$props.lang === "cn"){
                return top.cn;
            }else{
                return top.jp;
            }
                
            
        }
    }
}
</script>

